.subscribe{
  position: relative;
  &__inner{
    min-height: calc(100 * var(--vh));
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__img{
    pointer-events: none;
    position: absolute;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 2;
    width: 31.9375em;
    height: 31.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.subscribe-group{
  padding: 3.5em 3.5em 3em 10.5em;
  @media screen and (min-width: 577px){
    &:nth-child(even){
      .program{
        order: -1;
      }
    }
    &:nth-child(odd){
      border-right: .5em solid #fff;
    }
    &:nth-child(even){
      border-left: .5em solid #fff;
    }
    &:nth-child(1),
    &:nth-child(2){
      border-bottom: .5em solid #fff;
    }
    &:nth-child(3),
    &:nth-child(4){
      border-top: .5em solid #fff;
    }
    &:nth-child(4){
      .subscribe-group__top{
        padding-left: 20em;
      }
    }
  }
  &__top{
    max-width: 39.5em;
    .gradient-pink-text{
      display: inline-block;
      margin-left: .15em;
    }
  }
  &__desc{
    margin-top: 1em;
    @media screen and (min-width: 577px){
      &._pr{
        padding-right: 8em;
      }
      &._pl{
        padding-left: 5em;
      }
    }
  }
  &__grid{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
    max-width: 39.5em;
  }
}

.program,
.functions{
  &__items{
    margin-top: .625em;
  }
}

.program-item,
.function-item{
  position: relative;
  &:after,
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background: linear-gradient(180deg, #5D3EFE 0%, #FFC208 100%);
  }
  &:after{
    display: none;
    bottom: unset;
    top: 0;
  }
  &:first-child{
    &:after{
      display: block;
    }
  }
}

.function-item{
  padding: .375em 0;
  &.slideToggle-block--active{
    .function-item__btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
  &__show{
    display: flex;
    justify-content: space-between;
    cursor: pointer;
  }
  &__title{
    font-family: $font2;
    span{
      font-size: .8125em;
      line-height: 108%;
    }
  }
  &__btn{
    width: .875em;
    height: .875em;
    position: relative;
    span{
      @include centerAbsolute;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
    }
  }
  &__wrap{
    padding-top: .25em;
    .text12{
      line-height: 117%;
      p{
        line-height: inherit;
      }
    }
  }
}

.program-item{
  padding: .25em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__title,
  &__desc{
    font-size: .875em;
    line-height: 108%;
    font-family: $font2;
  }
  &__desc{
    min-width: 4.2em;
    text-align: right;
  }
}

.tariff{
  width: 14.789em;
  height: 14.79619em;
  font-family: $font2;
  color: #fff;
  position: absolute;
  &._active .tariff__inner{
    color: $black;
    &:before{
      opacity: 1;
    }
  }
  &__inner{
    width: calc(100% - 1em);
    height: calc(100% - 1em);
    position: absolute;
    top: .5em;
    left: .5em;
    text-align: center;
    transition: all .3s;
    &:before {
      opacity: 0;
      content: "";
      position: absolute;
      inset: 0;
      border-radius: inherit;
      background: linear-gradient(
                      15deg,
                      #ff0000,
                      #ffff00,
                      #00ff00,
                      #00ffff,
                      #0000ff
      )
      border-box;
    }
    &:before {
      animation: 2s linear infinite idle-blur;
    }

  }
  svg{
    position: relative;
    z-index: 2;
    width: 100%;
    height: auto;
    pointer-events: none;
  }
  &__title{
    font-size: 1em;
    margin-bottom: .25em;
    position: relative;
    z-index: 3;
  }
  &__num{
    font-size: 2.5em;
    position: relative;
    z-index: 3;
  }
  &._alpha{
    width: 15.53963em;
    height: 15.55494em;
    top: 6.5em;
    right: 0;
    z-index: 4;
    .tariff__inner{
      clip-path: polygon( 0.147% 52.126%,0.147% 52.126%,7.638% 50.911%,14.712% 48.632%,21.281% 45.375%,27.257% 41.228%,32.554% 36.278%,37.084% 30.613%,40.76% 24.318%,43.493% 17.482%,45.198% 10.192%,45.785% 2.534%,45.785% 2.534%,45.785% 2.308%,45.783% 2.083%,45.781% 1.857%,45.777% 1.633%,45.773% 1.408%,45.767% 1.184%,45.761% 0.959%,45.753% 0.736%,45.745% 0.512%,45.735% 0.288%,45.735% 0.288%,46.162% 0.254%,46.59% 0.223%,47.019% 0.196%,47.449% 0.172%,47.88% 0.152%,48.312% 0.135%,48.745% 0.122%,49.179% 0.113%,49.614% 0.107%,50.049% 0.105%,50.049% 0.105%,58.151% 0.757%,65.838% 2.643%,73.005% 5.661%,79.55% 9.709%,85.37% 14.684%,90.363% 20.484%,94.425% 27.006%,97.454% 34.148%,99.347% 41.807%,100% 49.881%,100% 49.881%,99.347% 57.955%,97.454% 65.614%,94.425% 72.756%,90.363% 79.278%,85.37% 85.078%,79.55% 90.053%,73.005% 94.101%,65.838% 97.12%,58.151% 99.006%,50.049% 99.657%,50.049% 99.657%,42.163% 99.04%,34.669% 97.253%,27.66% 94.389%,21.231% 90.544%,15.478% 85.81%,10.494% 80.283%,6.374% 74.057%,3.214% 67.226%,1.107% 59.884%,0.147% 52.126% );
      background: #221A4E;
      padding-top: 6em;
      padding-left: 1em;
    }
  }
  &._z{
    z-index: 3;
    border-radius: 50%;
    background: #A244FF;
    bottom: 1.5em;
    right: 8.06em;
    border: 1em solid #F6F6F6;
    .tariff__inner{
      padding-top: 5em;
    }
  }
  &._y{
    z-index: 2;
    border-radius: 50%;
    background: #AF79E5;
    bottom: 8em;
    left: 2em;
    border: 1em solid #F6F6F6;
    .tariff__inner{
      padding-top: 4em;
      padding-right: 3.5em;
    }
  }
  &._x{
    z-index: 2;
    border-radius: 50%;
    background: linear-gradient(90deg, #FFC208 0%, #FF8E75 100%);
    top: .75em;
    left: 9.375em;
    border: 1em solid #F6F6F6;
    .tariff__inner{
      padding-top: 4em;
    }
  }
}

@media screen and (min-width: 577px){
  @media screen and (max-width: 1200px){
    .program-item__title,
    .program-item__desc{
      font-size: .875em;
    }
    .function-item__title{
      span{
        font-size: 1em;
      }
    }
    .function-item{
      .text12{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .subscribe{
    padding: 2em 1em;
    &__inner{
      grid-template-columns: 100%;
      margin-top: 4.25em;
      gap: 1em;
    }
    &__img{
      position: relative;
      left: unset;
      top: unset !important;
      display: block;
      transform: translateX(-1em);
      font-size: .685em;
      margin: 0 auto;
    }
  }

  .subscribe-group{
    padding: 1.5em;
    @include setBackdrop;
    &__top{
      display: flex;
      justify-content: space-between;
      .text32{
        font-size: 1.625em;
      }
      .gradient-pink-text{
        font-size: 1.75em;
      }
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }
}
