.shop{
  padding: 3.5em 0;
  &__inner{
    position: relative;
  }
  &__content{
    grid-column: 1/3;
  }
}

.shop-search{
  .search-field{
    input{
      border-radius: 6.25em;
    }
    &__select{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 1.5em;
      padding-right: 2em;
      padding-left: .5em;
      border: none;
      background-position: right center;
      background-color: transparent;
    }
  }
}

.relevant-products{
  margin-top: 1.5em;
  display: inline-flex;
  align-items: center;
  padding: .5em 1em .5em 1.5em;
  @include setBackdrop;
  border-radius: 6.25em;
  &__items{
    display: flex;
    align-items: center;
    margin-left: .75em;
  }
  &__item{
    display: flex;
    align-items: center;
    padding: 0 .75em;
    height: 1.5em;
    border-radius: 2.5em;
    background-color: $lightPink;
    font-weight: 500;
    color: $purple2;
    @include lastM(0 .25em 0 0);
    span{
      font-size: 1.125em;
      line-height: 120%;
    }
  }
}

.filter{
  width: calc(100% / 3 - 5em / 3);
  position: absolute;
  right: 0;
  top: 0;
  padding: 2em;
  border-radius: 1.5em;
  background: $white100;
  backdrop-filter: blur(10px);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__refresh{
    display: none;
  }
  &__title{
    display: flex;
    align-items: center;
    .chat-item__count{
      margin-left: 1em;
    }
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(1em);
  }
  &__checks{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: 1em .5em;
    &._four-col{
      grid-template-columns: repeat(4,calc(25% - 1.5em / 4));
    }
    ._full{
      grid-column: 1/-1;
    }
  }
  &__btn{
    width: 100%;
  }
  .noUi-target{
    height: 1px;
    background: $black200;
    box-shadow: none;
    border: none;
  }
  .noUi-connect{
    background: $purple2;
  }
  .noUi-handle{
    border: none;
    background: $purple2;
    box-shadow: none;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    right: -4.5px;
    top: -4.5px;
    &:before,
    &:after{
      display: none;
    }
  }
  .noUi-marker{
    opacity: 0;
    pointer-events: none;
  }
  .noUi-pips-horizontal{
    padding-top: 0;
    height: unset;
  }
  .noUi-value{
    font-size: 1em;
    line-height: 125%;
    font-weight: 700;
    color: $black200;
  }
}

.filter-group{
  &__title{
    margin-bottom: 1em;
  }
  &__range{
    padding-bottom: 1em;
  }
}

.category-item{
  @include lastM(0 0 .5em);
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    &._active{
      .category-item__btn{
        svg{
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__link,
  &__title{
    font-size: 1.25em;
    line-height: 125%;
  }
  &__title{
    font-weight: 500;
  }
  &__btn{
    width: 1.5em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 .5em);
  }
  &__hidden{
    margin-top: .5em;
    padding-left: 2.5em;
    display: none;
  }
}

.shop-filter{
  &__mob{
    display: none;
  }
}

.products{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .75em));
  gap: 1.5em;
}

.stock-card,
.product-card{
  position: relative;
  &__inner{
    @include setBackdrop;
    clip-path: polygon( 0% 4.159%,0% 4.159%,0.058% 3.485%,0.225% 2.845%,0.492% 2.248%,0.851% 1.703%,1.292% 1.218%,1.806% 0.803%,2.384% 0.464%,3.017% 0.212%,3.696% 0.054%,4.412% 0%,74.607% 0%,74.607% 0%,75.694% 0.138%,76.728% 0.529%,77.697% 1.134%,78.593% 1.918%,79.406% 2.841%,80.126% 3.868%,80.743% 4.962%,81.249% 6.084%,81.633% 7.198%,81.885% 8.267%,81.885% 8.267%,81.974% 8.714%,82.083% 9.169%,82.214% 9.632%,82.368% 10.103%,82.545% 10.582%,82.747% 11.069%,82.975% 11.563%,83.23% 12.063%,83.512% 12.571%,83.824% 13.085%,83.824% 13.085%,84.515% 13.999%,85.32% 14.753%,86.222% 15.36%,87.204% 15.833%,88.248% 16.183%,89.339% 16.424%,90.459% 16.568%,91.59% 16.627%,92.717% 16.613%,93.822% 16.54%,93.822% 16.54%,94.738% 16.526%,95.639% 16.644%,96.506% 16.884%,97.318% 17.237%,98.058% 17.695%,98.707% 18.249%,99.244% 18.888%,99.651% 19.604%,99.91% 20.389%,100% 21.232%,100% 95.841%,100% 95.841%,99.942% 96.515%,99.775% 97.155%,99.508% 97.752%,99.149% 98.297%,98.708% 98.782%,98.194% 99.197%,97.616% 99.536%,96.983% 99.788%,96.304% 99.946%,95.588% 100%,4.412% 100%,4.412% 100%,3.696% 99.946%,3.017% 99.788%,2.384% 99.536%,1.806% 99.197%,1.292% 98.782%,0.851% 98.297%,0.492% 97.752%,0.225% 97.155%,0.058% 96.515%,0% 95.841%,0% 4.159% );
    min-height: 36.0625em;
    padding: 2.19em 2.5em;
  }
  &__img{
    width: 13.75em;
    height: 13.75em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      border-radius: 50%;
    }
  }
  &__rating{
    display: flex;
    align-items: center;
  }
  .rating-input{
    margin-right: .5em;
    &__item{
      width: 1.0625em;
    }
  }
}

.product-card{
  &__content{
    @include grid100gap(1em);
    margin-top: 1em;
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__sale{
    border: $border;
    padding: 0 1em;
    font-weight: 800;
    color: $purple2;
    margin-left: 1.5em;
    height: 2.25em;
    border-radius: 5em;
    display: flex;
    align-items: center;
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }

}

.stock-card{
  &__inner,
  &__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__content{
    margin-top: 1.5em;
  }
  &__info{
    margin-top: 1.5em;
    @include grid100gap(.5em);
  }
  &__rating{
    justify-content: center;
  }
  &__place{
    @include center;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .5em;
    }
  }
  &__tag{
    font-size: 1em;
    line-height: 125%;
    font-weight: 700;
    margin-right: .25em;
  }
}

.filter-reviews{
  @include grid100gap(.5em);
  &__item{
    display: flex;
    align-items: center;
    .rating-input{
      margin-right: 1em;
      &__item{
        width: 1.0625em;
      }
    }
  }
}


@media screen and (min-width: 577px) {
  @media screen and (max-width: 1200px) {
    .shop{
      .stock-card__tag,
      .text16{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .shop{
    padding-top: 1em;
    &__content{
      grid-column: 1/-1;
    }
  }

  .shop-search{
    .search-field{
      input{
        height: 3em;
        padding-left: 2.13em;
      }
      &__icon{
        width: .75em;
        left: 1em;
      }
      &__select{
        color: transparent;
        background-size: 1em auto;
        right: 1em;
      }
    }
  }

  .relevant-products{
    @include unsetBackdrop;
    display: block;
    &__title{
      font-weight: 500;
    }
    &__items{
      margin: .5em 0 0;
    }
    &__item{
      color: #fff;
      background-color: $purple2;
    }
  }

  .filter{
    z-index: 10;
    padding: 1.5em;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    top: 0;
    left: 0;
    width: 100%;
    &__title{
      align-items: unset;
      .chat-item__count{
        height: 1em;
        min-width: 1em;
        margin-left: .125em;
      }
    }
    &__refresh{
      display: flex;
      font-size: .875em;
      line-height: 115%;
      color: $purple2;
      text-decoration-line: underline;
    }
    &__checks{
      grid-template-columns: 100%;
      &._four-col{
        grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      }
    }
  }

  .category-item{
    &__btn{
      width: 1em;
    }
    &__hidden{
      padding-left: 1em;
    }
  }

  .shop-filter{
    margin-top: 2.125em;
    position: relative;
    &._active{
      .filter{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__mob{
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 1em;
      @include setBackdrop;
      height: 3.5em;
    }
    &__buttons{
      display: flex;
      align-items: center;
    }
    &__btn{
      width: 1.5em;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }

  .products{
    margin-top: 1.75em;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 2em 1em;
  }

  .stock-card,
  .product-card{
    &__inner{
      padding: 0;
      @include unsetBackdrop;
      clip-path: unset;
      min-height: unset;
    }
    .info__btn{
      top: 0;
      right: 0;
    }
    &__img{
      clip-path: polygon( 0% 3.333%,0% 3.333%,0.046% 2.793%,0.178% 2.28%,0.389% 1.801%,0.673% 1.365%,1.022% 0.976%,1.428% 0.643%,1.885% 0.372%,2.386% 0.17%,2.923% 0.044%,3.488% 0%,76.474% 0%,76.474% 0%,77.333% 0.109%,78.142% 0.417%,78.891% 0.894%,79.571% 1.514%,80.174% 2.246%,80.69% 3.063%,81.111% 3.935%,81.429% 4.834%,81.634% 5.732%,81.717% 6.599%,81.717% 6.599%,81.746% 7.174%,81.808% 7.767%,81.906% 8.377%,82.042% 9.004%,82.219% 9.648%,82.441% 10.307%,82.709% 10.981%,83.027% 11.669%,83.398% 12.371%,83.823% 13.085%,83.823% 13.085%,84.611% 14.102%,85.539% 14.92%,86.585% 15.554%,87.724% 16.024%,88.932% 16.347%,90.185% 16.541%,91.458% 16.624%,92.727% 16.613%,93.969% 16.527%,95.159% 16.382%,95.159% 16.382%,95.877% 16.337%,96.583% 16.405%,97.262% 16.578%,97.898% 16.847%,98.478% 17.203%,98.986% 17.64%,99.407% 18.149%,99.727% 18.721%,99.929% 19.348%,100% 20.022%,100% 96.667%,100% 96.667%,99.954% 97.207%,99.822% 97.72%,99.611% 98.199%,99.327% 98.635%,98.978% 99.024%,98.572% 99.357%,98.115% 99.628%,97.614% 99.83%,97.078% 99.956%,96.512% 100%,3.488% 100%,3.488% 100%,2.923% 99.956%,2.386% 99.83%,1.885% 99.628%,1.428% 99.357%,1.022% 99.024%,0.673% 98.635%,0.389% 98.199%,0.178% 97.72%,0.046% 97.207%,0% 96.667%,0% 3.333% );
      height: 11.25em;
      width: 100%;
      padding: 1.25em 1em;
      @include setBackdrop;
      border-radius: 1em;
    }
    &__content{
      gap: .5em;
      margin-top: .75em;
    }
    &__price{
      .text32{
        font-size: 1.5em;
      }
    }
    .rating-input{
      margin-right: .25em;
      &__item{
        width: .75em;
        margin-right: 0 !important;
      }
    }
  }

  .product-card{
    &__top{
      justify-content: space-between;
    }
    &__sale{
      height: 1.125em;
      padding: 0 .5em;
      color: #fff;
      background-color:$purple2;
      span{
        font-size: .5em;
      }
    }
    &__title{
      font-size: 1.125em;
    }
    .text20{
      font-size: .875em;
    }
  }

  .stock-card{
    &__content{
      display: block;
      text-align: left;
      width: 100%;
    }
    &__info{
      margin-top: .25em;
      gap: .25em;
    }
    &__tag{
      font-size: .625em;
      color: $purple2;
    }
    &__place{
      justify-content: unset;
      svg{
        width: .625em;
        margin-right: .125em;
      }
      path{
        stroke: $purple2;
      }
    }
    &__rating{
      justify-content: unset;
    }
  }
}
