$font-01: $font1;
$font-02: $font2;

$m-01: #FB8A8A;
$m-02: #FFEDC0;

$bg-01: #695681;
$bg-02: #36184F;
$bg-03: #32243E;

$g-01: linear-gradient(90deg, #FFEDC0 0%, #FF9D87 100%);
$g-02: linear-gradient(90deg, #8077EA 13.7%, #EB73FF 94.65%);

$cubic: cubic-bezier(0.4, 0.35, 0, 1.53);
$cubic2: cubic-bezier(0.18, 0.89, 0.32, 1.15);

$circleShadow: inset 5px 1.25em 2.5em rgba($bg-02, 0.25),
inset 5px 0px 5px rgba($bg-03, 0.3),
inset 5px 5px 1.25em rgba($bg-03, 0.25),
2px 2px 5px rgba(white, 0.2);


@mixin sm {
  @media screen and (max-width: 799px) {
    @content;
  }
}

@mixin height {
  @media screen and (max-height:660px) {
    @content;
  }
}

.error-page{
  background: #695681;
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}

.error404 {
  flex-grow: 1;
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: calc(100 * var(--vh));
  .container {
    margin: 0 auto;
    transition: all 0.4s ease;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    // Scene for the Parallax Effect
    .scene {
      position: absolute;
      width: 100vw;
      height: 100vh;
      vertical-align: middle;
    }

    // All elements' containers
    .one,
    .two,
    .three,
    .circle,
    .p404 {
      width: 60%;
      height: 60%;
      top: 20% !important;
      left: 20% !important;
      min-width: 25em;
      min-height: 25em;

      .error404__content {
        width: 37.5em;
        height: 37.5em;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        animation: content 0.8s cubic-bezier(1, 0.06, 0.25, 1) backwards;

        @keyframes content {
          0% {
            width: 0;
          }
        }

        // Pieces
        .piece {
          width: 12.5em;
          height: 5em;
          display: flex;
          position: absolute;
          border-radius: 5em;
          z-index: 1;

          animation: pieceLeft 8s cubic-bezier(1, 0.06, 0.25, 1) infinite both;


          @keyframes pieceLeft {
            0% {}

            50% {
              left: 80%;
              width: 10%;
            }

            100% {}

          }

          @keyframes pieceRight {
            0% {}

            50% {
              right: 80%;
              width: 10%;
            }

            100% {}

          }

        }
      }

      @include sm {
        width: 90%;
        height: 90%;
        top: 5% !important;
        left: 5% !important;
        min-width: 25em;
        min-height: 25em;
      }

      @include height {
        min-width: 25em;
        min-height: 25em;
        width: 60%;
        height: 60%;
        top: 20% !important;
        left: 20% !important;
      }
    }

    // Text and Button container
    .text {
      width: 60%;
      height: 40%;
      min-width: 25em;
      min-height: 31.25em;
      position: absolute;
      margin: 2.5em 0;
      animation: text 0.6s 1.8s ease backwards;

      @keyframes text {
        0% {
          opacity: 0;
          transform: translateY(2.5em);
        }
      }

      @include sm {
        min-height: 25em;
        height: 80%;
      }

      article {
        width: 25em;
        position: absolute;
        bottom: 0;
        z-index: 4;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);


        @include sm {
          width: 100%;
        }

        p {
          color: white;
          font-size: 1.125em;
          letter-spacing: 0.6px;
          margin-bottom: 2.5em;
          text-shadow: 6px 6px 10px $bg-03;
        }

        button {
          height: 2.5em;
          padding: 0 1.875em;
          border-radius: 3.125em;
          cursor: pointer;
          box-shadow: 0px 15px 1.25em rgba($bg-02, 0.5);
          z-index: 3;
          color: $bg-01;
          background-color: white;
          text-transform: uppercase;
          font-weight: 600;
          font-size: .75em;
          transition: all 0.3s ease;


          &:hover {
            box-shadow: 0px 10px 10px -10px rgba($bg-02, 0.5);
            transform: translateY(5px);
            background: $m-01;
            color: white;
          }
        }
      }
    }

    // The 404 Number
    .p404 {
      font-size: 12.5em;
      font-weight: 700;
      letter-spacing: 4px;
      color: white;
      display: flex !important;
      justify-content: center;
      align-items: center;
      position: absolute;
      z-index: 2;
      animation: anime404 0.6s cubic-bezier(0.3, 0.8, 1, 1.05) both;
      animation-delay: 1.2s;
      min-width: 3em;
      min-height: 3em;
      @media screen and (max-width: 992px){
        min-width: 8em;
        min-height: 8em;
        font-size: 8.25em;
      }
      @include sm {
        min-width: 5em;
        min-height: 5em;
        font-size: 5.25em;
        //top: 1em !important;
        //left: -2em !important;
        //opacity: 1;
      }

      @keyframes anime404 {
        0% {
          opacity: 0;
          transform: scale(3) skew(20deg, 20deg);
        }
      }

      &:nth-of-type(2) {
        color: $bg-02;
        z-index: 1;
        animation-delay: 1s;
        filter: blur(10px);
        opacity: 0.8;
      }


    }

    // Bigger Circle
    .circle {
      position: absolute;

      &:before {
        content: '';
        position: absolute;
        width: 50em;
        height: 50em;
        background-color: rgba($bg-02, 0.2);
        border-radius: 100%;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        box-shadow: $circleShadow;
        animation: circle 0.8s cubic-bezier(1, 0.06, 0.25, 1) backwards;


        @keyframes circle {
          0% {
            width: 0;
            height: 0;
          }
        }

        @include sm {
          width: 25em;
          height: 25em;
        }
      }
    }

    // Container 1
    .one {
      .error404__content {

        // Smaller Circle
        &:before {
          content: '';
          position: absolute;
          width: 37.5em;
          height: 37.5em;
          background-color: rgba($bg-02, 0.3);
          border-radius: 100%;
          box-shadow: $circleShadow;
          animation: circle 0.8s 0.4s cubic-bezier(1, 0.06, 0.25, 1) backwards;

          @include sm {
            width: 18.75em;
            height: 18.75em;
          }
        }

        .piece {
          background: $g-02;

          &:nth-child(1) {
            right: 15%;
            top: 18%;
            height: 1.875em;
            width: 7.5em;
            animation-delay: 0.5s;
            animation-name: pieceRight;
          }

          &:nth-child(2) {
            left: 15%;
            top: 45%;
            width: 13.125em;
            height: 3.125em;
            animation-delay: 1s;
            animation-name: pieceLeft;
          }

          &:nth-child(3) {
            left: 10%;
            top: 75%;
            height: 1.25em;
            width: 70px;
            animation-delay: 1.5s;
            animation-name: pieceLeft;
          }

        }


      }
    }

    // Container 2
    .two {
      .error404__content {
        .piece {
          background: $g-01;

          &:nth-child(1) {
            left: 0%;
            top: 25%;
            height: 2.5em;
            width: 7.5em;
            animation-delay: 2s;
            animation-name: pieceLeft;
          }

          &:nth-child(2) {
            right: 15%;
            top: 35%;
            width: 15em;
            height: 3.125em;
            animation-delay: 2.5s;
            animation-name: pieceRight;
          }

          &:nth-child(3) {
            right: 10%;
            top: 80%;
            height: 1.25em;
            width: 10em;
            animation-delay: 3s;
            animation-name: pieceRight;
          }

        }
      }
    }

    // Container 3
    .three {
      .error404__content {
        .piece {
          background: $m-01;

          &:nth-child(1) {
            left: 25%;
            top: 35%;
            height: 1.25em;
            width: 5em;
            animation-name: pieceLeft;
            animation-delay: 3.5s;
          }

          &:nth-child(2) {
            right: 10%;
            top: 55%;
            width: 12.5em;
            height: 2.5em;
            animation-name: pieceRight;
            animation-delay: 4s;
          }

          &:nth-child(3) {
            left: 40%;
            top: 68%;
            height: 1.25em;
            width: 5em;
            animation-name: pieceLeft;
            animation-delay: 4.5s;
          }

        }


      }
    }


  }
}
