select{
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  -o-appearance: none;
  appearance: none;
  background-image: url(../img/select-arrow2.svg);
  background-size: 1.5em auto;
  background-repeat: no-repeat;
  background-position: right 1em center;
  font-size: 1em;
  &::-ms-expand {
    display: none; // IE
  }
}

.fg {
  position: relative;
  margin: 0;
  label{
    font-family: $font2;
    display: block;
    margin-bottom: .67em;
  }
  input {
    height: 4em;
    border: none;
    padding: 0 1.5em;
    width: 100%;
    font-size: 1em;
    border-radius: .5em;
    background-color: $white100;
    color: $black200;
    &:focus {
      background-color: #fff;
    }
  }
  &._required{
    label:after{
      content: '*';
      color: $red;
    }
  }
}

.pass-toggle{
  position: absolute;
  right: 1.5em;
  bottom: 1.5em;
  height: 1em;
  svg{
    height: 100%;
    width: auto;
  }
  &:hover{
    path{
      fill: $primary;
    }
  }
}

.date-field{
  &__icon{
    height: 1.5em;
    width: auto;
    bottom: 1.25em;
    right: 1.5em;
    position: absolute;
    pointer-events: none;
  }
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field{
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.5em;
    @include lastM(0 .25em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover {
      cursor: pointer;
      path{
        fill: $purple2;
      }
      &~.rating-input__item {
        path{
          fill: $purple2;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path{
    fill: $purple2;
  }
}

.m-check {
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 3;
    cursor: pointer;
  }
  input {
    &:checked~label {
      &::after {
        opacity: 1;
      }
    }
  }
  label {
    padding-left: 3em;
    position: relative;
    min-height: 2em;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 1em;
    line-height: 125%;
    font-weight: 400;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 2em;
      height: 2em;
      left: 0;
      top: 0;
    }
    &:before {
      border-radius: .5em;
      border: 1px solid $black200;
      transition: all .100s;
    }
    &:after{
      z-index: 2;
      background-image: url(../img/check.svg);
      @include bgImgSize(1.5em);
      opacity: 0;
    }
  }
  a{
    color: $primary;
  }
  &._black{
    label:after{
      background-image: url(../img/check4.svg);
    }
  }
}

.m-select{
  select{
    height: 3em;
    padding: 0 2.75em 0 1em;
    border-radius: 6.25em;
    background-color: $lightPink;
    border: none;
  }
  &._black{
    select{
      @media screen and (min-width: 577px){
        background-color: $black400;
        background-image: url(../img/select-arrow3.svg);
        color: #fff;
        background-size: 1em auto;
        height: 2.5em;
      }
    }
  }
}

.fg2{
  position: relative;
  label{
    font-size: 1.25em;
    line-height: 125%;
    font-weight: 500;
    display: block;
    margin-bottom: 1em;
    font-family: $font2;
  }
  input,
  select{
    width: 100%;
    height: 3.5em;
    padding: 0 1.5em;
    font-size: 1em;
    border-radius: .5em;
    border: 1px solid $black200;
    color: $black200;
    &:focus{
      border-color: $primary;
    }
    &::placeholder{
      color: $black200;
    }
  }
  &__icon{
    position: absolute;
    bottom: 1em;
    left: 1.5em;
    width: 1.5em;
    height: auto;
    &~input{
      padding-left: 3.5em;
    }
  }
}

@media screen and (max-width: 576px){
  .fg{
    label{
      margin-bottom: .75em;
    }
    input{
      height: 3.25em;
      padding: 0 1em;
    }
  }

  .pass-toggle{
    bottom: 1.125em;
    right: 1em;
  }

  .rating-input{
    &__item{
      width: .75em;
      margin: 0 !important;
    }
  }

  .m-select{
    select{
      background-color: $purple2;
      color: #fff;
      background-image: url(../img/select-arrow3.svg);
      background-size: .85em;
      font-size: .75em;
      height: 2em;
      padding-right: 2em;
    }
  }

  .fg2{
    input,
    select{
      height: 3.25em;
      background-size: 1em auto;
    }
  }

  .m-check {
    label {
      padding-left: 2em;
      min-height: 1.5em;
      &:before,
      &:after{
        width: 1.5em;
        height: 1.5em;
      }
      &:after{
        background-size: 1.25em auto;
      }
    }
  }
}
