.team{
  padding-bottom: 4em;
  &__inner{
    @include setBackdrop;
    padding: 2.5em;
  }
  &__items{
    margin-top: 2.5em;
    @include grid100gap(1.5em);
  }
}

.member-card{
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 68.25em;
  &__user{
    display: flex;
    align-items: center;
    .avatar{
      width: 5em;
      height: 5em;
      margin-right: 1.5em;
    }
    &-info{
      @include grid100gap(.25em);
    }
  }
  &__btn{
    flex-shrink: 0;
    height: 2.25em;
    padding: 0 1em;
    font-weight: 400;
    border-radius: 6.25em;
    span{
      font-size: 1em;
    }
  }
}

.pay{
  &__items{
    margin-top: 2.5em;
    max-width: 95em;
  }
}

.pay-item{
  &__img{
    border-radius: .5em;
    background-color: $white100;
    border: $border2;
    margin-bottom: 1.5em;
    width: 14.25em;
    height: 10em;
    max-width: 100%;
    padding: 1em;
    @include center;
    img{
      width: 8.25em;
      max-width: 100%;
      display: block;
    }
  }
}

.gift{
  margin-top: 2.5em;
  @include grid100gap(2.5em);
  &__grid{
    max-width: 95em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    height: 4em;
    width: 16.25em;
  }
}

.levels{
  margin-top: 2.5em;
  display: flex;
  &__wrap{
    display: flex;
    flex-direction: row-reverse;
  }
}

.level-item{
  width: 7.5em;
  height: 7.5em;
  padding: 1em;
  border-radius: 50%;
  border: $border;
  text-align: center;
  background: rgba(255, 255, 255, 0.40);
  backdrop-filter: blur(10px);
  &:not(:last-child){
    margin-left: -.5em;
  }
  &:not(._active){
    .level-item__num{
      @include linearText;
    }
  }
  &._active{
    color: #fff;
    background: linear-gradient(90deg, #FFC208 0%, #FF8E75 100%);
    .purple-text2{
      color: #fff;
    }
  }
  &__count{
    margin-top: .625em;
  }
}

.partners{
  margin-top: 2.5em;
  .text40{
    span{
      @include linearText;
    }
  }
  .team__items{
    margin-top: 1.5em;
  }
}

.promo{
  margin-top: 2.5em;
  @include grid100gap(2.5em);
}

.promo-code{
  display: flex;
  align-items: center;
  &__num{
    padding: 0 1em;
    border-radius: 0.5em;
    background: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
    height: 3em;
    display: flex;
    align-items: center;
    border: $border;
  }
  &__btn{
    width: 1.5em;
    margin-left: .75em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.promo-invite{
  @include grid100gap(.75em);
  &__link{
    word-break: break-word;
  }
  &__nav{
    margin-top: .75em;
    display: flex;
  }
}

.promo-follow{
  display: flex;
  align-items: center;
  &__social{
    width: 5em;
    height: 5em;
    background: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
    border: $border;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    margin-right: 1.6875em;
    svg{
      width: 2.5em;
      height: auto;
    }
  }
  &__nav{
    margin-top: .75em;
    display: flex;
  }
}


@media screen and (max-width: 576px){
  .team{
    padding: 2.5em 0;
    &__inner{
      padding: 1.5em;
    }
    &__top{
      padding-bottom: 1.5em;
      position: relative;
      border-bottom: 1px solid $primary;
      .text40{
        font-size: 1em;
      }
    }
    &__items{
      gap: 1.25em;
      margin-top: 1.5em;
    }
  }

  .member-card{
    display: block;
    padding-right: 6em;
    position: relative;
    .avatar{
      width: 1.875em;
      height: 1.875em;
      margin-right: .75em;
    }
    &__user{
      &-info{
        gap: 0;
      }
    }
    &__desc{
      padding: .3125em 0 0 2.625em;
    }
    &__btn{
      position: absolute;
      top: 0;
      right: 0;
      height: 1.5em;
      padding: 0 .75em;
      span{
        font-size: .75em;
      }
    }
  }

  .pay{
    &__items{
      margin-top: 1.5em;
    }
  }

  .pay-item{
    &__img{
      background: rgba(255, 255, 255, 0.40);
      backdrop-filter: blur(10px);
      margin-bottom: .75em;
      width: 100%;
      height: 9em;
    }
    .text24{
      line-height: 100%;
      span{
        font-size: .75em;
      }
    }
  }

  .gift{
    margin-top: 1.5em;
    gap: 1em;
    .text32{
      font-size: 1em;
    }
    .fg{
      input{
        height: 4em;
        padding: 0 1.5em;
      }
    }
    &__nav{
      margin-top: 1em;
    }
  }

  .levels{
    margin-top: 1.5em;
  }

  .level-item{
    width: 4.5em;
    height: 4.5em;
    padding: .5em;
    &:not(:last-child){
      margin-left: -.7em;
    }
    &__count{
      margin-top: .25em;
    }
    .text20{
      line-height: 114%;
    }
  }

  .partners{
    &__top{
      padding-bottom: .5em;
      border-bottom: 1px solid $primary;
      .text40{
        font-size: 1em;
      }
    }
  }

  .promo{
    margin-top: 1.5em;
    gap: 1.5em;
  }

  .promo-code{
    &__num{
      height: 2.5em;
      .text24{
        font-size: 1.25em;
      }
    }
  }

  .promo-invite{
    .text24{
      max-width: 75%;
    }
  }

  .promo-follow{
    align-items: unset;
    &__social{
      width: 4em;
      height: 4em;
      margin-right: 1.4375em;
    }
    &__nav{
      margin-top: .5em;
    }
  }
}
