.orders-top{
  display: grid;
  grid-template-columns: calc(100% - 16.75em) 16.25em;
  gap: 2em .5em;
  &__btn{
    padding: 0;
    width: 100%;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: 1em;
    }
  }
}

.orders{
  .top-authors__inner{
    padding: 1.5em;
    margin-top: 1.5em;
  }
  &__items{

  }
}

.order-card{
  border-bottom: $border;
  padding: 1.5em 0;
  display: grid;
  grid-template-columns: 20em calc(100% - 30.375em) 8.375em;
  align-items: center;
  gap: 1em;
  &:last-child{
    padding-bottom: 0;
    border: none;
  }
  .avatar{
    margin-right: 1.5em;
    &:before{
      border-width: 2px;
    }
  }
  .author-item{
    &__content{
      @include grid100gap(.5em);
    }
  }
  .rating-input{
    margin-right: .5em;
    &__item{
      width: 1.0625em;
    }
  }
  &__top{
    display: flex;
    align-items: center;
    svg{
      width: 1.5625em;
      height: auto;
      margin-right: .5em;
    }
  }
  &__content{
    @include grid100gap(.5em);
  }
  &__desc{
    max-width: 31em;
  }
  &__tags{
    display: flex;
    flex-wrap: wrap;
  }
  &__tag{
    font-size: .875em;
    line-height: 125%;
    font-weight: 700;
    margin-right: .67em;
  }
  &__action{
    @include grid100gap(.5em);
  }
  &__btn{
    justify-content: unset;
    height: 2.5em;
    width: 100%;
    padding: 0 .75em;
    font-weight: 500;
    border-radius: 2.5em;
    span{
      font-size: .75em;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .5em;
    }
  }
}

@media screen and (min-width: 577px) {
  @media screen and (max-width: 1200px) {
    .order-card{
      grid-template-columns: 20em calc(100% - 31.375em) 9.375em;
    }

    .order-card__btn span{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .orders-top{
    grid-template-columns: 100%;
    &~.shop-filter{
      margin-top: .5em;
    }
  }

  .orders{
    .top-authors__inner{
      margin-top: .5em;
    }
    &__items{
      margin-top: 1.5em;
      @include grid100gap(1.5em)
    }
  }

  .order-card{
    padding: 0;
    border: none;
    grid-template-columns: calc(100% - 6em) 5em;
    gap: .25em 1em;
    padding-left: 1.25em;
    &__content{
      order: -1;
      grid-column: 1/-1;
      gap: .25em;
    }
    &__top{
      margin-left: -1.25em;
      align-items: flex-start;
      width: calc(100% + 1.25em);
      .text24{
        line-height: 100%;
      }
      svg{
        width: 1em;
        margin-right: .25em;
      }
    }
    &__tag{
      font-size: .5em;
      color: $purple2;
      margin-right: .5em;
    }
    .avatar{
      width: 1.875em;
      height: 1.875em;
      margin-right: .75em;
    }
    .author-item{
      &__content{
        gap: 0;
        .text16{
          font-size: .875em;
          line-height: 115%;
        }
        .text10-mob{
          font-size: .625em;
        }
      }
    }
    .rating-input{
      margin-right: .25em;
      &__item{
        width: .75em;
      }
    }
    &__action{
      gap: unset;
      display: flex;
      justify-content: flex-end;
    }
    &__btn{
      padding: 0;
      width: 1.5em;
      height: 1.5em;
      border-radius: 50%;
      margin-left: .125em;
      justify-content: center;
      svg{
        width: .875em;
        margin: 0;
      }
      span{
        display: none;
      }
    }
  }
}
