@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
._blur-bg{
  background-image: url(../img/bg-blur.svg);
  background-size: cover;
  border-radius: 1.5em;
  border: 1px solid $white100;
}

._grid-column3{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
  gap: 2.5em;
}

.slideToggle-block {
  &__hidden {
    height: 0;
    overflow: hidden;
    transition: all .425s;
  }
}

.apexcharts-text tspan{
  //font-size: .75em;
}

@media screen and (max-width: 576px){
  ._grid-column3{
    grid-template-columns: 100%;
    gap: 1.5em;
  }

  .apexcharts-text tspan{
    font-size: .5em;
  }
}
