.catalog-item{
  &__inner{
    padding: 2em;
    @include setBackdrop;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 43em) 38em;
    align-items: center;
    gap: 3.25em 5em;
  }
}

.gallery-collection{
  &__img{
    display: block;
    @include imgCover(47.9%);
    border-radius: 1.5em;
    img{
      border-radius: inherit;
    }
  }
  &__small{
    max-width: 100%;
    width: 43.6875em;
    margin: 5em auto 0;
    .gallery-collection__img{
      border: 1px solid transparent;
      transition: all .3s;
    }
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: repeat(3,calc(100% / 3 - 1em));
        gap: 1.5em;
      }
      &-slide{
        cursor: pointer;
        &:hover{
          .gallery-collection__img{
            border-color: $purple2;
          }
        }
      }
      &-slide-thumb-active{
        .gallery-collection__img{
          border-color: $purple2;
        }
      }
    }
  }
}

.product-info{
  &__top{
    display: flex;
    align-items: center;
    .rating-input{
      margin: 0 .5em 0 1.5em;
    }
  }
  &__title{
    font-size: 3em;
    font-family: $font2;
    line-height: 127%;
  }
  &__desc{
    margin-top: 1.5em;
    button{
      font-weight: 700;
    }
  }
  &__cart{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__btn{
    padding: 0 3.5em;
    svg{
      margin-right: .5em;
      width: 2em;
      height: auto;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.5em;
  }
}

.colors{
  display: flex;
}

.color{
  margin-right: 1em;
  flex-shrink: 0;
  display: block;
  &._active{
    pointer-events: none;
    .color__inner {
      border-color: $purple2 !important;
    }
  }
  &__inner{
    display: block;
    border-radius: 50%;
    width: 3.5em;
    height: 3.5em;
    position: relative;
    border-width: 2px;
    border-style: solid;
  }
}

.socials{
  display: flex;
  align-items: center;
}

.social{
  width: 1.75em;
  @include lastM(0 1.5em 0 0);
  svg{
    width: 100%;
    height: auto;
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding: 3.25em 0;
    &__inner{
      padding: 2.5em 1.5em 1.5em;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .gallery-collection{
    &__small{
      margin-top: 1.25em;
      .swiper{
        &-wrapper{
          grid-template-columns: repeat(3,calc(100% / 3 - .25em));
          gap: .375em;
        }
      }
    }
    &__img{
      border-radius: .5em;
    }
  }

  .product-info{
    .rating-input{
      margin: 0 .25em 0 .375em;
    }
    &__top{
      margin-bottom: 1.44em;
    }
    &__title{
      font-size: 1.5em;
      line-height: 100%;
    }
    &__desc{
      margin-top: .5em;
      .text16-mob{
        line-height: 125%;
      }
    }
    &__bottom{
      padding-top: 3.5em;
      position: relative;
      margin-top: 1.5em;
    }
    .colors{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }
    &__cart{
      flex-wrap: wrap;
      justify-content: unset;
      margin: 0;
    }
    &__price{
      .text32{
        font-size: 1.5em;
      }
    }
    &__select{
      margin-left: 1.25em;
    }
    &__btn{
      margin-top: 1.25em;
      padding: 0 1.5em;
      border-radius: 1em;
      svg{
        width: 1.5em;
      }
    }
  }

  .color{
    margin-right: .5em;
    &__inner{
      width: 2em;
      height: 2em;
    }
  }

  .social{
    @include lastM(0 .75em 0 0);
  }
}
