.top-authors{
  padding: 5em 0;
  &__inner{
    padding: 2em;
    @include setBackdrop;
    @media screen and (min-width: 577px){
      background: rgba(246, 246, 246, 0.20);
    }
  }
}

.authors-table{
  margin-top: 2.5em;
  table{
    width: 100%;
    border-collapse: collapse;
  }
  th{
    font-size: 1em;
    line-height: 125%;
    padding: .625em 0;
    text-align: left;
    font-weight: 400;
  }
}

.author-row{
  &:not(:last-child){
    td{
      border-bottom: $border2;
    }
  }
  td{
    padding: 1.5em 0;
  }
  .avatar{
    width: 5em;
    height: 5em;
    margin-right: 1.5em;
  }
  &__rating{
    display: flex;
    align-items: center;
    svg{
      width: 1.75em;
      height: auto;
      margin-left: .5em;
    }
  }
  &__item{
    svg{
      display: none;
    }
  }
}


@media screen and (min-width: 577px) {
  @media screen and (max-width: 1200px) {
    .authors-table th{
      font-size: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .top-authors{
    padding: 1.5em 0;
    &__inner{
      padding: 1.5em 1.75em;
    }
    &__top{
      padding-bottom: .875em;
      border-bottom: $border;
      .text32{
        font-size: 1em;
      }
    }
  }

  .authors-table{
    margin-top: 0;
    table,td{
      display: block;
    }
    thead{
      display: none;
    }
    tbody{
      @include grid100gap(1.5em);
    }
    td{
      padding: 0;
      border: none !important;
      &:nth-child(1){
        grid-column: 1/-1;
      }
    }
    tr{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: .25em 1em;
      padding-left: 2.625em;
      position: relative;
    }
    .avatar{
      position: absolute;
      top: 0;
      left: 0;
      width: 1.875em;
      height: 1.875em;
    }
    .author-item__content{
      width: 100%;
      padding-bottom: .25em;
      border-bottom: $border;
    }
    .text32{
      font-size: .875em;
    }
  }

  .author-row{
    &__rating{
      svg{
        width: .75em;
        margin-left: .125em;
      }
    }
    &__item{
      display: flex;
      align-items: center;
      svg{
        display: block;
        width: .75em;
        margin-right: .125em;
      }
    }
  }
}
