.wallet-page{
  padding: 2em -0px 4em;
  &__inner{
    @include setBackdrop;
    padding: 2.5em 3.5em 3.125em;
    @media screen and (min-width: 577px){
      background: rgba(246, 246, 246, 0.20);
    }
  }
  &__top{
    display: grid;
    grid-template-columns: 28.6875em 14.8125em 28.6875em calc(100% - 76.2375em);
    gap: 1.35em;
  }
  .wallet{
    min-height: 15.6875em;
    padding: 2em 2em 1.75em;
    border-radius: 2.5em;
    &__top{
      img{
        width: 2.8125em;
        margin-right: 1em;
      }
      span{
        font-size: 1.25em;
      }
    }
    &__sum{
      margin-top: 1.56em;
      span{
        font-size: 2.5em;
      }
    }
    &__desc{
      font-size: .75em;
    }
  }
  &__bottom{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 52.75em calc(100% - 57.75em);
    gap: 5em;
  }
}

.wallet-balance{
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
  }
  &__badges{
    display: flex;
    margin-left: 1.5em;
    .badge{
      font-weight: 400;
    }
  }
  .wallet{
    background-image: url(../img/wallet-bg.png);
  }
}

.wallet-action{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 2.84em;
  &__btn{
    margin-top: 1em;
    border-radius: 1em;
    height: 4.25em;
    width: 100%;
    color: #fff;
    background: linear-gradient(90deg, rgba(86, 86, 86, 0.00) 0%, #FFC208 100%);
    font-weight: 700;
    @include center;
    &:hover{
      opacity: .8;
    }
    ._view2{
      display: none;
    }
    &._type1{
      svg{
        margin-left: .5em;
      }
    }
    &._type2{
      background: linear-gradient(90deg, #5D3EFE 0%, rgba(86, 86, 86, 0.00) 100%);
      svg{
        margin-right: .5em;
      }
    }
    svg{
      width: 1.5em;
      height: auto;
    }
    span{
      font-size: 1em;
    }
  }
}

.delegate{
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
    padding-left: 1.25em;
  }
  &__hint{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    @include center;
    background: $purple2;
    border-radius: 50%;
    color: #fff;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  .wallet{
    background-image: url(../img/wallet-bg2.png);
    &:before{
      background: conic-gradient(from 0deg at 50.00% 49.80%, rgba(93, 62, 254, 0.70) 23.62495332956314deg, rgba(131, 58, 180, 0.70) 90deg, rgba(255, 172, 55, 0.70) 180deg, rgba(255, 132, 55, 0.70) 270deg, rgba(255, 55, 55, 0.70) 316.1249613761902deg);
    }
  }
}

.benefit{
  @media screen and (min-width: 577px){
    padding-top: 4em;
    .wallet{
      padding: 1.5em;
      min-height: 11.875em;
      border-radius: 1.89544em;
      &__top{
        img{
          width: 2.3125em;
          margin-right: .76em;
        }
        span{
          font-size: .95em;
        }
      }
      &__sum{
        margin-top: 1.18em;
        span{
          font-size: 1.9em;
        }
      }
      &__desc{
        font-size: .57em;
      }
    }
  }
  .wallet{
    background-image: url(../img/wallet-bg3.png);
    &:before{
      display: none;
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.3125em;
    font-weight: 400;
    border-radius: 6.25em;
    height: 3em;
    svg{
      width: 1.5em;
      height: auto;
      margin-left: .5em;
    }
  }
}

.wallet-stat{
  margin-top: 2em;
  display: flex;
  justify-content: space-between;
  &__group{
    @media screen and (min-width: 577px){
      min-height: 3.625em;
      flex-grow: 1;
      &:not(:last-child){
        padding-right: 2em;
        margin-right: 2em;
        border-right: $border2;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: .5em;
  }
  &__progress{
    display: flex;
    align-items: center;
    margin-left: .5em;
    color: #009F85;
    svg{
      width: 1em;
      height: auto;
      margin-right: .25em;
    }
    &._red{
      color: #FF4040;
    }
  }
  &__rating{
    margin-left: .5em;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
}

.balance-history{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    width: 2em;
    height: 2em;
    margin-left: .5em;
    border-radius: 0.5em;
    background-color: $white100;
    @include center;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.5em);
  }
}

.transaction-card{
  display: grid;
  grid-template-columns: 20em calc(100% - 32em) 10em;
  gap: 1em;
  align-items: center;
  &__content{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 4em;
    height: 4em;
    border-radius: 1em;
    background-color: $white100;
    @include center;
    margin-right: 1.5em;
    flex-shrink: 0;
    svg{
      width: 1.5em;
      height: auto;
    }
  }
  &__info{
    @include grid100gap(.25em);
  }
  &__nums{
    color: $white300;
  }
  &__price{
    text-align: right;
    .text16{
      color: $purple2;
    }
  }
}

.faq{
  &__items{
    @include grid100gap(1em);
  }
}

.qa-card{
  padding: 1em;
  border-radius: .5em;
  background-color: $white100;
  &._active{
    .qa-card__btn svg{
      transform: rotate(-180deg);
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 500;
    cursor: pointer;
  }
  &__btn{
    width: 1.5em;
    margin-left: .5em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding-top: 1em;
    display: none;
  }
  &__text{
    font-size: 1em;
    line-height: 150%;
    &>*{
      line-height: inherit;
    }
  }
}


@media screen and (min-width: 577px) {
  @media screen and (max-width: 1200px) {
    .wallet-page{
      .text16{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .wallet-page{
    &__inner{
      padding: 1.5em;
    }
    &__top{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
    .wallet{
      min-height: 10.375em;
      padding: 1em;
      border-radius: 1em;
      &__top{
        img{
          width: 1.5em;
          margin-right: .5em;
        }
        span{
          font-size: 1.125em;
        }
      }
      &__sum{
        margin-top: 1.5em;
        span{
          font-size: 2em;
        }
      }
    }
    &__bottom{
      margin-top: 2.5em;
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .wallet-balance{
    &__top{
      padding-bottom: .875em;
      border-bottom: $border;
      justify-content: space-between;
      .text32{
        font-size: 1em;
      }
      .badge{
        height: 1.5em;
        span{
          font-size: .875em;
        }
      }
    }
  }

  .wallet-action{
    padding: 0 1.3em;
    margin-top: -.5em;
    &__btn{
      margin-top: .5em;
      height: 3em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .delegate{
    position: relative;
    &__top{
      margin: 0;
      .text32{
        display: none;
      }
    }
    &__hint{
      z-index: 2;
      position: absolute;
      top: .75em;
      right: .75em;
      margin: 0;
      background: rgba(27, 27, 27, 0.70);
    }
  }

  .benefit{
    &__btn{
      margin-top: 1em;
    }
  }

  .wallet-stat{
    margin-top: 2.5em;
    display: block;
    &__group{
      padding: 1em 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: $border;
      &:first-child{
        padding-top: 0;
      }
      &:last-child{
        padding-bottom: 0;
        border-bottom: none;
      }
    }
    &__title{
      font-size: 1.125em;
    }
    &__bottom{
      margin: 0 0 0 2em;
    }
    &__price{
      .text16{
        font-size: 1.125em;
      }
      .text24{
        font-size: 1.5em;
      }
    }
    &__progress{
      margin-left: .13em;
      svg{
        width: .9375em;
        margin-right: 0;
      }
      .text16{
        font-size: 1.125em;
      }
    }
    &__rating{
      margin-left: .125em;
      svg{
        width: .75em;
      }
    }
  }

  .balance-history{
    &__top{
      padding-bottom: .875em;
      border-bottom: $border;
    }
    &__btn{
      width: 1.5em;
      height: 1.5em;
      border-right: .25em;
      svg{
        width: .75em;
      }
    }
    &__items{
      margin-top: .75em;
      gap: .875em;
    }
  }

  .transaction-card{
    grid-template-columns: 8em calc(100% - 14em) 5em;
    gap: .5em;
    &__img{
      width: 1.5em;
      height: 1.5em;
      border-radius: .25em;
      background-color: $black400;
      margin-right: .25em;
      svg{
        width: .875em;
      }
      path{
        stroke: #fff;
      }
    }
    &__info{
      gap: 0;
    }
    &__nums{
      color: $white300;
    }
    &__price{
      .text16{
        color: $purple2;
      }
    }
  }

  .qa-card{
    padding: .75em .94em;
    &__btn{
      width: .75em;
    }
  }
}
