.footer{
  padding: 5em 0;
  border-radius: 2.5em 2.5em 0 0;
  background: rgba(246, 246, 246, 0.20);
  backdrop-filter: blur(10px);
  border: 1px solid $white100;
  .container{
    padding: 0 7.5em;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 3.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__conf{
    text-decoration: underline;
  }
}


@media screen and (max-width: 576px){
  .footer{
    border-radius: 1em 1em 0 0;
    margin: 0 auto;
    padding: 1.625em 1em .69em;
    width: calc(100% - 2em);
    background: rgba(255, 255, 255, 0.40);
    .container{
      padding: 0;
    }
    &__inner{
      flex-direction: column;
      text-align: center;
    }
    &__logo{
      width: 3em;
      margin-bottom: 1.44em;
    }
    &__conf{
      margin-bottom: .375em;
    }
  }
}
