.gaming{
  padding: 4em 0;
  &__inner{
    @include setBackdrop;
    padding: 2.5em;
    @include grid100gap(2.5em);
    @media screen and (max-width: 577px){
      border-radius: 2.5em;
      background: rgba(246, 246, 246, 0.20);
    }
  }
}

.prize,
.profile-filled,
.events{
  display: grid;
  align-items: center;
  padding: 4em 5em;
  @include setBackdrop;
  border-radius: 2.5em;
}

.events{
  grid-template-columns: 48.875em calc(100% - 52.5em);
  gap: 2em 3.625em;
  align-items: flex-start;
  @media screen and (min-width: 577px){
    &.events2{
      grid-template-columns: calc(100% - 52.5em)  48.875em;
      align-items: center;
      .events-info{
        max-width: 29em;
        .text16{
          max-width: 23.75em;
        }
      }
    }
  }
}

.awards{
  display: grid;
  grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
  gap: 2.5em;
}

.award-card{
  &__top{
    padding: 2.25em;
    border-radius: 1.5em;
    border: 2.25px solid #EBECFD;
    background: #FCFCFC;
    box-shadow: 0px 2.25px 9px 0px rgba(110, 116, 134, 0.12);
  }
  &__title{
    text-transform: uppercase;
    text-align: center;
    line-height: 150%;
  }
  &__img{
    display: block;
    width: 100%;
    margin-top: 1.125em;
    filter: drop-shadow(0px 18px 22.5px rgba(1, 5, 51, 0.08));
  }
  &__content{
    margin-top: .5em;
  }
  &__subtitle{
    text-align: center;
  }
  &__bottom{
    margin-top: .5em;
    display: flex;
    align-items: center;
  }
  &__users{
    display: flex;
    align-items: center;
    margin-right: .5em;
  }
  .avatar{
    &:nth-child(2){
      z-index: 2;
    }
    &:nth-child(3){
      z-index: 3;
    }
    &:before{
      border-color: $white100;
      border-width: 3px;
    }
    &:not(:first-child){
      margin-left: -.5em;
    }
  }
}

.important-info,
.events-info,
.profile-filled__progress{
  @include grid100gap(1.5em);
}

.important-info{
  padding: 1.5em 2em;
  border-radius: 1.5rem;
  border: $border2;
  background: $white100;
  &__top{
    display: flex;
    align-items: center;
    color: $purple2;
    svg{
      width: 1.5em;
      height: auto;
      margin-right: .5em;
    }
  }
}

.profile-filled{
  grid-template-columns: calc(100% - 51.5em) 46.5em;
  gap: 1.5em 5em;
  .test-progress{
    margin: 0;
  }
}

.prize{
  grid-template-columns: calc(100% - 47em) 38.5em;
  gap: 1em 8.5em;
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .75em));
    gap: 1.5em;
  }
}

.prize-card,
.prize-collection{
  border-radius: 1.25em;
  background: #FFF;
  box-shadow: 0 0 4em 0 rgba(0, 0, 0, 0.10);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.prize-card{
  padding: 2.125em 2em 2.44em;
  &__medal{
    width: 5.125em;
    display: block;
    margin-bottom: 1em;
  }
  &__badge{
    height: 1.5em;
    font-weight: 500;
    background: rgba(#E4B337,.1);
    color: #E8AE1A;
    margin: .8125em 0 0 !important;
  }
  &__info{
    font-family: $font3;
    margin-top: 1em;
    text-align: center;
    @include grid100gap(1em);
    .text14{
      line-height: 170%;
    }
    .progress{
      margin: .5em auto 0;
    }
  }
}

.progress{
  width: 3.0625em;
  position: relative;
  svg{
    width: 100%;
    height: auto;
  }
  &__num{
    @include centerAbsolute;
    color: #AAA3BF;
  }
  .circle-chart{
    transform: rotate(-90deg);
  }
}

.prize-collection{
  padding: 2.56em .44em 0;
  max-height: 32em;
  &__items{
    padding: 0 1.8em;
    margin-top: .81em;
    overflow-y: auto;
    height: calc(100% - 4em);
    width: 100%;
    @include scroll;
  }
}

.prize-item{
  margin-bottom: 1em;
  display: flex;
  align-items: center;
  font-feature-settings: 'clig' off, 'liga' off;
  font-family: $font3;
  &__icon{
    width: 1.9375em;
    margin-right: .67em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
      filter: drop-shadow(0 .5em .75em rgba(234, 176, 32, 0.40));

    }
  }
  .text14{
    font-weight: 600;
  }
  .text12{
    color: rgba(21, 30, 35, 0.60);
  }
}

.prize-badges{
  max-width: 39em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(8,calc(12.5% - 7em / 8));
    gap: 1em;
  }
}

.prize-badge{
  display: block;
  &:hover{
    img{
      opacity: 1;
    }
  }
  img{
    transition: all .3s;
    opacity: .1;
    filter: drop-shadow(0 .5em .625em rgba(1, 5, 51, 0.08));
    display: block;
    width: 100%;
  }
}


@media screen and (min-width: 577px){
  @media screen and (max-width: 1200px){
    .gaming{
      .text20{
        font-size: 1.5em;
      }
      .text16{
        font-size: 1.25em;
      }
      .text14{
        font-size: 1em;
      }
      .text12{
        font-size: .875em;
      }
    }
    .story-item{
      .text16{
        font-size: 1.25em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .gaming{
    padding: 1.5em 0 3em;
    &__inner{
      background: rgba(255, 255, 255, 0.40);
      border-radius: 1.5em;
      padding: 1.5em;
      gap: 0;
    }
    &__top{
      padding-bottom: .875em;
      border-bottom: $border;
      .text32{
        font-size: 1em;
      }
    }
  }

  .prize,
  .profile-filled,
  .events{
    grid-template-columns: 100%;
    margin-top: 1.12em;
    padding: 0;
    @include unsetBackdrop;
  }

  .awards{
    grid-template-columns: repeat(3,calc(100% / 3 - .25em));
    gap: .375em;
  }

  .award-card{
    &__top{
      padding: 1em .75em;
      border-radius: 1em;
    }
    &__title{
      font-size: .75em;
    }
    &__img{
      margin-top: .5em;
    }
    &__bottom{
      flex-direction: column;
      .text12{
        line-height: 100%;
      }
    }
    &__users{
      margin: 0 0 .5em;
      .avatar{
        width: 1.5em;
        height: 1.5em;
        &:before{
          border-width: 2px;
        }
        &:not(:first-child){
          margin-left: -.5em;
        }
      }
    }
  }

  .important-info,
  .events-info{
    gap: .75em;
  }

  .important-info{
    margin-top: .75em;
    padding: 1em;
    &__top{
      svg{
        width: 1em;
        margin-right: .125em;
      }
    }
  }

  .prize,
  .profile-filled,
  .events2{
    margin-top: 1.5em;
    padding-top: 1.5em;
    border-top: $border;
  }

  .events2{
    gap: 1.5em;
  }

  .profile-filled{
    .test-progress{
      &__title{
        font-size: .875em;
        max-width: 70%;
        line-height: 115%;
      }
      &__desc{
        font-size: .625em;
      }
    }
    .progressbar{
      margin-top: .5em;
    }
  }

  .prize{
    &._hide-mob{
      display: none;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
      order: 2;
    }
    .prize-collection,
    .prize-card{
      font-size: .5em;
      .text24{
        font-size: 1.25em;
      }
    }
  }

  .prize-collection{
    padding-top: 1.5em;
    max-height: 30em;
    &__items{
      &::-webkit-scrollbar {
        width: 3px;
        height: 3px;
      }
    }
  }

  .prize-card{
    padding: 1.5em;
  }
}
