@keyframes idle {
    0% {
        filter: hue-rotate(0deg);
    }
    100% {
        filter: hue-rotate(360deg);
    }
}
@keyframes idle-blur {
    0% {
        filter: hue-rotate(0deg) blur(20px);
    }
    100% {
        filter: hue-rotate(360deg) blur(20px);
    }
}


