.feed{
  padding: 3.75em 0;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__content{
    grid-column: 1/3;
  }
  .info{
    margin-bottom: 1.5em;
  }
  &__top{
    @include grid100gap(1.5em);
    margin-bottom: 1.5em;
  }
}

