  display: flex;
  &__wrap{
    position: relative;
    .news__btn{
      right: 0;
      left: unset;
      top: 50%;
      transform: translateY(-50%);
    }
  }
  .text16{
    line-height: 125%;
  }
}

.story-plus{
  text-align: center;
  white-space: nowrap;
  margin-right: 1.5em;
  &__btn{
    width: 100%;
    max-width: 7.5em;
    margin: 0 auto 1em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.story-item{
  margin-right: 1.5em;
  text-align: center;
  &__img{
    border-radius: 50%;
    width: 7.5em;
    height: 7.5em;
    padding: 1px;
    background: linear-gradient(180deg, #5D3EFE 0%, #FFC208 100%);
    margin: 0 auto 1em;
    img{
      width: 100%;
      height: 100%;
      border-radius: inherit;
      object-fit: cover;
    }
  }
}

.feed-search{
  display: grid;
  grid-template-columns: calc(100% - 17.75em) 16.25em;
  gap: .75em 1.5em;
  &__btn{
    height: 4em;
    padding: 0;
    svg{
      width: 1.5em;
      margin-right: 1em;
    }
    span{
      font-size: 1em;
    }
  }
}

.feed-content{
  padding: 3em;
  @include setBackdrop;
  background-color: rgba(246, 246, 246, 0.20);
  @include grid100gap(2.5em);
  border-radius: 2.5em;
  &__top{
    max-width: 60.625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__tabs{
    display: flex;
    align-items: center;
  }
  &__tab{
    margin-left: 1.5em;
    &._active{
      font-weight: 700;
    }
    span{
      font-size: 1em;
      line-height: 125%;
    }
  }
}

.feed-item{
  padding: 2em;
  @include setBackdrop;
  border-radius: 2.5em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__user{
    display: flex;
    align-items: center;
    &-info{
      @include grid100gap(.75em);
    }
    .avatar{
      width: 5em;
      height: 5em;
      margin-right: 1.5em;
      &:before{
        border-color: $white100;
        border-width: 4px;
      }
    }
  }
  &__action{
    display: flex;
  }
  &__btn{
    margin-left: 1em;
    width: 5em;
    height: 5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      clip-path: polygon( 50% 100%,50% 100%,58.11% 99.346%,65.804% 97.451%,72.978% 94.419%,79.529% 90.353%,85.355% 85.355%,90.353% 79.529%,94.419% 72.978%,97.451% 65.804%,99.346% 58.11%,100% 50%,100% 50%,99.961% 48.008%,99.845% 46.036%,99.654% 44.086%,99.389% 42.158%,99.051% 40.254%,98.642% 38.375%,98.163% 36.524%,97.616% 34.701%,97.002% 32.908%,96.323% 31.146%,96.323% 31.146%,96.093% 30.714%,95.792% 30.354%,95.432% 30.066%,95.024% 29.852%,94.581% 29.711%,94.114% 29.643%,93.634% 29.649%,93.154% 29.729%,92.684% 29.882%,92.238% 30.11%,92.238% 30.11%,91.484% 30.545%,90.705% 30.941%,89.903% 31.295%,89.079% 31.606%,88.234% 31.873%,87.37% 32.095%,86.488% 32.27%,85.59% 32.397%,84.677% 32.474%,83.75% 32.5%,83.75% 32.5%,81.114% 32.287%,78.614% 31.672%,76.282% 30.686%,74.153% 29.365%,72.26% 27.74%,70.635% 25.847%,69.314% 23.718%,68.328% 21.386%,67.713% 18.886%,67.5% 16.25%,67.5% 16.25%,67.526% 15.323%,67.603% 14.41%,67.73% 13.512%,67.905% 12.63%,68.127% 11.766%,68.394% 10.921%,68.705% 10.097%,69.059% 9.295%,69.455% 8.516%,69.89% 7.762%,69.89% 7.762%,70.118% 7.316%,70.271% 6.846%,70.351% 6.366%,70.357% 5.886%,70.289% 5.419%,70.148% 4.976%,69.934% 4.568%,69.646% 4.208%,69.286% 3.907%,68.854% 3.677%,68.854% 3.677%,67.092% 2.998%,65.299% 2.384%,63.476% 1.837%,61.625% 1.358%,59.746% 0.949%,57.842% 0.611%,55.914% 0.346%,53.964% 0.155%,51.992% 0.039%,50% 0%,50% 0%,41.89% 0.654%,34.196% 2.549%,27.022% 5.581%,20.471% 9.647%,14.645% 14.645%,9.647% 20.471%,5.581% 27.022%,2.549% 34.196%,0.654% 41.89%,0% 50%,0% 50%,0.654% 58.11%,2.549% 65.804%,5.581% 72.978%,9.647% 79.529%,14.645% 85.355%,20.471% 90.353%,27.022% 94.419%,34.196% 97.451%,41.89% 99.346%,50% 100% );
      border: 1px solid $white100;
      background-color: $lightPink;
    }
    &-num{
      position: absolute !important;
      font-size: .875em;
      line-height: 130%;
      font-weight: 700;
      right: 0;
      top: .7em;
      color: $purple2;
    }
    svg{
      width: 2em;
      height: auto;
      position: relative;
      z-index: 2;
    }
    &._comments{
      svg{
        width: 2.625em;
      }
    }
    &._saves{
      svg{
        width: 1.875em;
      }
    }
  }
  &__text{
    margin-top: 1.5em;
    button{
      font-weight: 700;
    }
  }
  &__tags{
    display: flex;
    margin-top: .5em;
  }
  &__tag{
    font-weight: 700;
    margin-right: .5em;
    span{
      font-size: 1em;
      line-height: 123%;
    }
  }
  &__img{
    margin-top: 1.5em;
    width: 100%;
    border-radius: 1em;
    display: block;
  }
}

.gallery{
  display: grid;
  grid-template-columns: calc(100% - 25.875em) 12.4375em 12.4375em;
  gap: .5em;
  margin-top: 1.5em;
}

.gallery-item{
  position: relative;
  &:nth-child(1){
    grid-row: 1/3;
    .gallery-item__img{
      padding-bottom: 72.3%;
    }
  }
  &:nth-child(5)~.gallery-item{
    display: none;
  }
  &__img{
    min-height: 100%;
    @include imgCover(97.48%);
    img{
      border-radius: 1em;
    }
  }
  &__count{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 1em;
    background-color: rgba(#000,.7);
    color: #fff;
    @include center;
  }
}

.posts,
.authors{
  @include setBackdrop;
  padding: 2em;
  border-radius: 2.5em;
}

.posts{
  &__grid{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
  }
}

.post-card{
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(41%);
    img{
      border-radius: 1.5em;
    }
  }
  &__content{
    margin-top: 1em;
    @include grid100gap(.5em);
    .text12{
      @include truncate(2);
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    padding-top: .5em;
  }
  .badge{
    height: 1.5em;
    padding: 0 .5em;
    svg{
      width: 1em;
      margin-left: .5em;
    }
  }
  &__btn,
  &__action{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: .5em;
    font-weight: 500;
    color: $purple2;
    svg{
      width: 1.25em;
      height: auto;
      margin-right: .5em;
    }
    span{
      font-size: .875em;
      line-height: 125%;
    }
  }
}

.author-card{
  padding: 1.5em 0;
  display: grid;
  grid-template-columns: 16.25em calc(100% - 38em) 20.75em;
  gap: 1em;
  &:not(:last-child){
    border-bottom: 1px solid $black200;
  }
}

.author-item{
  display: flex;
  align-items: center;
  .avatar{
    width: 7.5em;
    height: 7.5em;
    margin-right: .75em;
    &:before{
      border-width: 3px;
    }
  }
  &__info{
    @include grid100gap(.5em);
  }
  &__nav{
    display: flex;
    margin-top: 1em;
    .badge{
      height: 2.125em;
      padding: 0 .75em;
      svg{
        margin: 0 .5em 0 0;
        width: 1.125em;
      }
    }
  }
}

.article-card{
  display: flex;
  align-items: center;
  &__img{
    width: 6.75em;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      border-radius: 1em;
      width: 100%;
      display: block;
    }
  }
  &__info{
    @include grid100gap(.5em);
  }
  &__nav{
    margin-top: .5em;
  }
}


@media screen and (max-width: 576px){
  .feed{
    padding: .5em 0 1em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__content{
      grid-column: 1/-1;
    }
    &__sidebar{
      display: none;
    }
    &__top{
      gap: 1em;
    }
  }

  .stories{
    border-radius: 0;
    clip-path: unset;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: .75em 1em;
    min-height: unset;
    overflow-x: auto;
    @include nullScroll;
    &__wrap{
      .news__btn{
        display: none;
      }
    }
  }

  .story-plus{
    flex-shrink: 0;
    width: 3.5em;
    margin-right: .75em;
    white-space: unset;
    text-align: center;
    &__btn{
      max-width: 100%;
      margin-bottom: 0;
    }
    .text16{
      font-size: .5625em;
    }
  }

  .story-item{
    margin-right: .75em;
    &__img{
      padding: 2px;
      width: 3.5em;
      height: 3.5em;
      margin-bottom: .5em;
    }
    .text16{
      font-size: .625em;
      line-height: 100%;
    }
  }

  .feed-search{
    order: 2;
    grid-template-columns: 100%;
    &__btn{
      height: 3.5em;
    }
    .search-field{
      input{
        height: 3em;
        border-radius: 1.5em;
        padding-left: 2.13em;
      }
      &__icon{
        width: .75em;
        left: 1em;
      }
      &__voice{
        width: 1.25em;
        right: .625em;
      }
    }
  }

  .feed-content{
    background: rgba(255, 255, 255, 0.40);
    gap: 1.5em;
    padding: 1.5em;
    border-radius: 1.5em;
    &__top{
      padding-bottom: .875em;
      border-bottom: $border;
      .text40{
        font-size: 1em;
        line-height: 150%;
      }
    }
    &__tab{
      margin-left: .5em;
      span{
        font-size: .625em;
      }
    }
    .authors,
    .posts{
      display: none;
    }
  }

  .feed-item{
    padding: 0;
    background-color: transparent;
    border-radius: 0;
    border: none;
    @include unsetBackdrop;
    &__user{
      .avatar{
        margin-right: .75em;
        width: 1.875em;
        height: 1.875em;
        &:before{
          border-width: 2px;
          border-color: $purple2;
        }
      }
      &-info{
        gap: 0;
      }
    }
    &__btn{
      margin-left: .5em;
      height: unset;
      width: unset;
      display: flex;
      align-items: center;
      &:before{
        display: none;
      }
      svg{
        width: 1.125em !important;
        margin-right: .5em;
      }
      &-num{
        position: static;
        font-size: .75em;
        top: -.875em;
        right: -.875em;
      }
      &._saves{
        display: none;
      }
    }
    &__text{
      margin-top: .75em;
    }
    &__tag{
      color: $purple2;
      span{
        line-height: 125%;
      }
    }
    &__img{
      margin-top: .75em;
      border-radius: .5em;
    }
  }

  .gallery{
    grid-template-columns: calc(100% - 4.375em) 3.875em;
    margin-top: .75em;
  }

  .gallery-item{
    &:nth-child(1){
      grid-row: 1/4;
      .gallery-item__img{
        padding-bottom: 73.5%;
      }
    }
    &:nth-child(4)~.gallery-item{
      display: none;
    }
    &__img{
      padding-bottom: 87%;
      img{
        border-radius: .25em;
      }
    }
  }
}
