.main{
  padding: 0 0 4.35em;
  &__inner{
    position: relative;
  }
  &__top,
  &__bottom{
    display: grid;
    grid-template-columns: 31.6875em 31.6875em;
    justify-content: space-between;
  }
  &__center{
    display: flex;
    justify-content: space-between;
    margin-top: 1.5em;
  }
  &__bottom{
    margin-top: 1.5em;
  }
  &__mob{
    .relevant,
    .news{
      display: none;
    }
    .wallet-link{
      position: absolute;
      bottom: 0;
      z-index: 2;
      left: 50%;
      transform: translateX(-40%);
    }
  }
}

.indicators,
.info,
.relevant,
.news,
.user,
.balance{
  position: relative;
  &__inner{
    height: 100%;
    border-radius: 1.5em;
    padding: 2em;
    min-height: 27em;
    border: 1px solid $white100;
    background-color: rgba(255, 255, 255, 0.40);
    backdrop-filter: blur(10px);
  }
  &__btn{
    position: absolute;
    width: 5em;
    height: 5em;
    z-index: 2;
    svg{
      width: 2.5em;
    }
  }
}

