.effect-1,
.effect-2 {
  border-radius: 30%;
  display: none;
  mix-blend-mode: multiply;
  height: 97%;
  opacity: .5;
  position: absolute;
  transform: translate(-7%,-7%);
  width: 97%;
  z-index: -1;
}

.effect-1 {
  animation: rotate 1.8s linear infinite;
  background: cyan;
}

.effect-2 {
  animation: rotate 1.2s linear reverse infinite;
  background: #e7a9ff;
}

@keyframes rotate {
  0% {
    top: 0;
    left: 8%;
  }
  25% {
    top: 8%;
    left: 0%;
  }
  50% {
    top: 16%;
    left: 8%;
  }
  75% {
    top: 8%;
    left: 16%;
  }
  100% {
    top: 0;
    left: 8%;
  }
}

.wallet{
  background-image: url(../img/bg2.png);
  background-size: cover;
  background-position: center;
  border-radius: 1.78em;
  padding: 1.42em;
  min-height: 11.18em;
  position: relative;
  color: #fff;
  font-family: $font3;
  display: flex;
  flex-direction: column;
  &:hover{
    //color: $black;
    &:after{
      //opacity: 1;
    }
    .effect-1,
    .effect-2{
      display: block;
    }
  }
  &:after {
    opacity: 0;
    content: "";
    position: absolute;
    inset: 0;
    border-radius: inherit;
    background: linear-gradient(
                    15deg,
                    #ff0000,
                    #ffff00,
                    #00ff00,
                    #00ffff,
                    #0000ff
    )
    border-box;
  }
  &:after {
    animation: 2s linear infinite idle-blur;
  }
  &:before{
    content: '';
    position: absolute;
    width: calc(100% - 1.42em);
    height: 9.13em;
    bottom: -.36em;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 1.78106rem;
    background: conic-gradient(from 0deg at 50.00% 49.80%, rgba(107, 49, 128, 0.70) 23.62495332956314deg, rgba(131, 58, 180, 0.70) 90deg, rgba(255, 132, 55, 0.70) 168.75deg, rgba(255, 172, 55, 0.70) 245.62500715255737deg, rgba(133, 67, 184, 0.70) 316.1249613761902deg);
