    z-index: -1;
  }
  &__top,
  &__sum,
  &__desc{
    position: relative;
    z-index: 2;
  }
  &__top{
    display: flex;
    align-items: center;
    font-weight: 600;
    img{
      width: 2em;
      margin-right: .71em;
    }
    span{
      font-size: .9em;
    }
  }
  &__sum{
    margin-top: 1.1em;
    font-weight: 700;
    span{
      font-size: 1.78em;
