.text40{
  font-size: 2.5em;
  line-height: 127.5%;
}

.text32{
  font-size: 2em;
  line-height: 128%;
}

.text h3,
.profile-nav__link,
.fg label,
.text24{
  font-size: 1.5em;
  line-height: 130%;
}

.text20{
  font-size: 1.25em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 118%;
}

.text16{
  font-size: 1em;
  line-height: 125%;
}

.text14{
  font-size: .875em;
  line-height: 115%;
}

.text12{
  font-size: .75em;
  line-height: 125%;
}

.text10{
  font-size: .75em;
  line-height: 130%;
}

.text8{
  font-size: .5em;
}

.lh150{
  line-height: 150%;
}

.text h3,
.profile-nav__link,
.font2{
  font-family: $font2;
}

.primary-text{
  color: $primary;
}

.purple-text2{
  color: $purple2;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.gradient-pink-text{
  background: var(--gradient-pink, linear-gradient(90deg, #5D3EFE 0%, #C64DFF 100%));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@media screen and (min-width: 577px){
  @media screen and (max-width: 1200px){
    .text40{
      font-size: 3.25em;
    }
    .text32{
      font-size: 2.5em;
    }
    .text h3,
    .profile-nav__link,
    .fg label,
    .text24{
      font-size: 1.75em;
    }
    .member-card{
      .text16{
        font-size: 1.25em;
      }
    }
    .level-item{
      .text12{
        font-size: 1em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .text40{
    font-size: 1.5em;
  }

  .text h3,
  .fg label,
  .text24{
    font-size: 1em;
    line-height: 150%;
  }

  .text20{
    font-size: 1em;
    line-height: 125%;
  }

  .text16-mob{
    font-size: 1em;
    line-height: 125%;
  }

  .text14-mob{
    font-size: .875em;
  }
  //
  //.text12-mob{
  //  font-size: .75em;
  //}
  //
  //.text10-mob{
  //  font-size: .625em;
  //}
}
