.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &__bg {
        background: rgba(#C2AFF6, 0.9);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }
    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        width: 57.5em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        overflow-y: auto;
        overflow-x: hidden;
        max-height: calc(100 * var(--vh));
        border-radius: 1.5em;
        background: rgba(255, 255, 255, 0.80);
        backdrop-filter: blur(10px);
        padding: 5em;
    }
    &__inner{
        display: flex;
        align-items: center;
        flex-direction: column;
    }
    &__info{
        @include grid100gap(1em);
        margin-top: 2.5em;
        text-align: center;
        max-width: 24.875em;
        &._big{
            max-width: 39.25em;
            .modal__nav{
                display: grid;
                grid-template-columns: repeat(2,calc(50% - .75em));
                gap: 1.5em;
            }
        }
    }
    &__btn{
        width: 100%;
        height: 3.5625em;
        border-radius: 2.5em;
        font-weight: 400;
        svg{
            width: 1.5em;
            height: auto;
            margin-right: .5em;
            &:last-child{
                margin: 0 0 0 .5em;
            }
        }
    }
}

.img-input{
    width: 27.4375em;
    height: 27.4375em;
    position: relative;
    border-radius: 50%;
    border: $border;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.20) 0%, rgba(0, 0, 0, 0.00) 100%);
    overflow: hidden;
    input{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        cursor: pointer;
        opacity: 0;
    }
    img{
        width: 100%;
        display: block;
    }
    &__mark{
        @include centerAbsolute;
        font-size: 10em;
        font-family: $font2;
        color: $purple2;
        pointer-events: none;
    }
}


@media screen and (min-width: 577px) {
    @media screen and (max-width: 1200px) {
        .modal{
            .text16{
                font-size: 1.25em;
            }
        }
    }
}



@media screen and (max-width: 576px){
    .modal{
        &__dialog{
            padding: 3em .75em;
        }
        &__info{
            margin-top: 1.5em;
            gap: .75em;
            .text32{
                font-size: 1em;
            }
            &._big{
                .modal__nav{
                    grid-template-columns: 100%;
                    gap: .5em;
                    margin-top: .75em;
                }
                .modal__btn{
                    margin: 0;
                }
            }
        }
        &__nav{
            margin-top: .25em;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }
        &__btn{
            width: auto;
            padding: 0 2em;
            margin-top: .5em;
            height: 3em;
            svg{
                width: 1em;
                margin-right: .25em;
            }
        }
    }

    .img-input{
        width: 12.75em;
        height: 12.75em;
        &__mark{
            font-size: 6.5em;
        }
    }
}
